export default {
	data() {
		return {
			isShow1:true,
			isShow2:true,
			isShow3:false,
			isShow4:false,
		};
	},
	onLoad() {
		var type = uni.getStorageSync("type");
		// var type = '07';
		if(type == "05"){
			this.isShow4 = true;
		}else if(type == "06"){
			this.isShow3 = true;
		}else if(type == "07"){
			this.isShow3 = true;
			this.isShow4 = true;
		}
	},
	methods:{
		goPage(e){
			uni.navigateTo({
				url:e
			})
		},
	}
}
